<template>
	<view class="main">
		<view class="btns">
			<view class="btn-con" v-for="(item, index) in musicStyles">
				<view class="btn" :class="{'btn-before':index!=current,'btn-after':index==current}"
					@click="clickBtn(index)">
					<image :src="item.icon"></image>
				</view>
				<view>{{item.style}}</view>
			</view>
		</view>

		<swiper :current="current" @change="changeSwiper">
			<swiper-item>
				<view class="musics">
					<view class="music" :class="{'btn-before':music!=item.type,'btn-after2':music==item.type}" v-for="(item,index) in musicStyles[current].musics" @click="chooseMusic(item)">
						<image :src="item.icon"></image>
						<view class="name">{{item.name}}</view>
					</view>
				</view>
			</swiper-item>

			<swiper-item>
				<view class="musics">
					<view class="music" :class="{'btn-before':music!=item.type,'btn-after2':music==item.type}" v-for="(item,index) in musicStyles[current].musics" @click="chooseMusic(item)">
						<image :src="item.icon"></image>
						<view class="name">{{item.name}}</view>
					</view>
				</view>
			</swiper-item>

			<swiper-item>
				<view class="musics">
					<view class="music" :class="{'btn-before':music!=item.type,'btn-after2':music==item.type}" v-for="(item,index) in musicStyles[current].musics" @click="chooseMusic(item)">
						<image :src="item.icon"></image>
						<view class="name">{{item.name}}</view>
					</view>
				</view>
			</swiper-item>
		</swiper>
		
		<view class="createBtn">
			<luButtonRipple class="button" rippleBackgroundColor="red" :rippleOpacity="0.6" @rippleTap="createMusic">
				<image src="../static/images/styles/music/create_music.png"></image>
			</luButtonRipple>
		</view>
		
		<loading ref="loading" :shadeShow="true" :shadeClick="true" :custom="false" :type="2">
			<text style="font-size: 10px;position: fixed;bottom: 10px;">AI正在谱曲中...</text>
		</loading>
	</view>
</template>

<script>
	import luButtonRipple from '@/components/lu-button-ripple/lu-button-ripple.vue';
	import loading from '@/components/xuan-loading/xuan-loading.vue';
	export default {
		components:{
			luButtonRipple,
			loading
		},
		data() {
			return {
				current: 0,
				style:"古典",
				music:"beiduofen",
				musicStyles: [{
						style: "古典",
						icon:"../static/images/styles/music/gudian.png",
						musics:[
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"贝多芬",
								type:"beiduofen"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"巴赫",
								type:"bahe"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"莫扎特",
								type:"mozhate"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"海顿",
								type:"haidun"
							},
						]
					},
					{
						style: "民歌",
						icon:"../static/images/styles/music/minge.png",
						musics:[
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"贝多芬",
								type:"beiduofen"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"巴赫",
								type:"bahe"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"莫扎特",
								type:"mozhate"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"海顿",
								type:"haidun"
							},
						]
					},
					{
						style: "蓝调",
						icon:"../static/images/styles/music/landiao.png",
						musics:[
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"贝多芬",
								type:"beiduofen"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"巴赫",
								type:"bahe"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"莫扎特",
								type:"mozhate"
							},
							{
								icon:"../static/images/styles/music/beiduofen.jpg",
								name:"海顿",
								type:"haidun"
							},
						]
					}
				],
			}
		},
		methods: {
			clickBtn(index) {
				if (this.current !== index) {
					this.current = index;
					this.style = this.musicStyles[this.current].style;
				}
			},
			changeSwiper(e) {
				this.current = e.detail.current;
			},
			chooseMusic(item){
				this.music = item.type;
			},
			createMusic() {
				let _this = this;
				this.$refs.loading.open();
				uni.request({
					// url: "http://duing.site:5667/getMusic?style=" + _this.style + "&music=" + _this.music,
					url: "http://duing.site:5667/getMusicAdd",
					timeout: 60000 * 5,
					success(res_) {
						_this.$store.state.musicPlayer.src = res_.data;

						_this.$store.state.music = {
							poster: "../static/images/styles/yaogun.jpg",
							name: "AI曲风创作",
							author: "AI",
							duration: "00.00",
							currentTime: "00:00",
						}
						_this.$refs.loading.close();

						setTimeout(function() {
							_this.$store.state.play_status = true;
							_this.$store.commit("playMusic");
						}, 2000);

						uni.navigateTo({
							// url:"../views/play?style=" + style
							url: "../views/play"
						});
					}
				});
			}
		}
	}
</script>

<style>
	.main {
		background-color: #d3dfdf;
		width: 100vw;
		height: 100vh;
	}

	.btns {
		width: 100vw;
		height: 20vh;
		background-color: #d3dfdf;
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.btn-con {
		margin: 0 auto;
		text-align: center;
		font-size: 12px;
	}

	.btn {
		width: 20vw;
		height: 20vw;
		border-radius: 10vw;
		background-color: #d3dfdf;
		margin-bottom: 8px;
		display: flex;
		align-items: center;
	}
	
	.btn image{
		width: 12vw;
		height: 12vw;
		margin: 0 auto;
	}

	.btn-before {
		background: linear-gradient(145deg, #e2efef, #bec9c9);
		box-shadow: 11px 11px 26px #8d9595,
			-11px -11px 26px #ffffff;
	}

	.btn-after {
		background: linear-gradient(145deg, #adb7b7, #d6efef);
		box-shadow: 11px 11px 26px #8d9595,
			-11px -11px 26px #ffffff;
	}
	
	.btn-after2 {
		background: linear-gradient(145deg, #9ba4a4, #d0efef);
		box-shadow: 11px 11px 26px #8d9595,
			-11px -11px 26px #ffffff;
	}

	swiper {
		height: 80vh;
	}
	
	.musics{
		display: flex;
		flex-direction: column;
	}
	
	.music{
		width: 90vw;
		height: 20vw;
		margin: 5px auto;
		margin-top: 10px;
		display: flex;
		flex-direction: row;
		align-items: center;
		border: #c4cdcb 1px solid;
		border-radius: 3vw;
		/* background: linear-gradient(145deg, #e2efef, #bec9c9);
		box-shadow: 11px 11px 26px #8d9595,
			-11px -11px 26px #ffffff; */
	}
	
	.music image{
		width: 14vw;
		height: 14vw;
		border-radius: 7vw;
		margin-left: 10px;
	}
	
	.name{
		margin-left: 20vw;
	}
	
	.createBtn {
		position: fixed;
		bottom: 25%;
		right: 25%;
	}
	
	.button {
		width: 55px;
		height: 55px;
		border: 1px solid #DCDCDC;
		border-radius: 40px;
		box-shadow: 1px 1px 2px 2px #A9A9A9;
		margin-top: 60px;
	}
	
	.button image {
		width: 35px;
		height: 35px;
	}
</style>
