				let _this = this;
				let style = this.lyricStyles[this.current];
				let name = this.lyricNames[this.current];
				let start = "小";
				this.$refs.loading.open();
				uni.request({
					url: "http://duing.site:5669/getLyrics?singer=" + style + "&start=" + start,
					success(res_) {
						// console.log(res_)
						_this.$store.state.lyrics = res_.data;
						_this.$refs.loading.close();
				
						uni.navigateTo({
							url: "../views/lyrics?style=" + style + "&name=" + name,
