	}

	.button {
		width: 50px;
		height: 50px;
		border: 1px solid #DCDCDC;
		border-radius: 40px;
		box-shadow: 1px 1px 2px 2px #A9A9A9;
		margin-top: 60px;
	}

	.button image {
